/* Scss Document */
@charset "utf-8";
/* CSS Document */

.wrapper_detail {
    .wrap_item_top {
        .thumb_video {
            margin-bottom: 10px;
        }

        .date {
            color: var(--gray3);
            font-size: 14px;
            margin-bottom: 5px;
        }
    }

    .title_video {
        padding: 0;
        color: var(--colormain);
        font-size: 18px;
        margin-bottom: 10px;
    }

    .lead_video {
        font-size: 16px;
        line-height: 1.5;
    }

    .social {
        border-top: 1px solid var(--border);
    }

    .related {
        background: var(--bg-agray);
        padding: 10px;
        margin-bottom: 20px;
        width: 100%;
        float: left;

        li {
            border-bottom: 1px solid var(--white);
            font-size: 16px;
            line-height: 1.31;
            margin-bottom: 5px;
            padding-bottom: 5px;

            a {
                color: var(--blue);
            }

            &:last-of-type {
                border: none;
                padding: 0;
                margin: 0;
            }
        }
    }
}

.box_category {
    width: 100%;
    float: left;
    margin-bottom: 20px;
}

.playlist {
    clear: both;
    background: var(--bg-agray);
    margin-bottom: 30px;

    .title_playlist {
        width: 100%;
        float: left;
        background: #eee;
        color: #999;
        font-size: 16px;
        line-height: 1.31;
        border: none;
    }

    .ic_playlist_24 {
        margin-right: 10px;
    }

    .list_video_folder {
        padding: 15px;
        overflow-y: scroll;
        max-height: 380px;

        .thumb_video {
            width: 130px;
        }

        .item_video {
            border-bottom: 1px solid #ddd;
            margin-bottom: 10px;
            padding-bottom: 10px;

            &:last-child {
                border: none;
                margin-bottom: 0;
                padding-bottom: 0;
            }
        }
    }

    &.collapse {
        .list_video_folder {
            display: none;
        }
    }
}

.info_video_top {
    padding: 0 15px 0 15px;
}

.social {
    margin-top: 15px;
    padding-top: 15px;
    border-top: 1px solid #dddddd;
}

.wrap_social {
    &.right {
        margin-top: -2px;
    }
}

.wrap_count_like {
    width: auto;
    height: 28px;
    line-height: 28px;
    position: relative;
    display: inline-block;
    background: var(--blue);
    color: var(--white);
    font-size: 13px;
    text-indent: 25px;
    border-radius: 3px;
    padding: 0 10px 0 0;
}

.wrap_count_like:before {
    content: '';
    width: 5px;
    height: 2px;
    background: var(--white);
    position: absolute;
    left: 7px;
    top: 14px;
    transform: rotate(45deg);
}

.fb-like {
    display: inline-block !important;
    float: left !important;
}

.wrap_count_like:after {
    content: '';
    width: 10px;
    height: 2px;
    background: var(--white);
    position: absolute;
    left: 9px;
    top: 12px;
    transform: rotate(-45deg);
}

/**Author mail**/
.author_mail {
    text-align: right;
    font-size: 16px;
    color: #666;
    margin-bottom: 10px;
    line-height: 1.31;
}

.author_mail strong {
    color: var(--color1);
}

.author_mail .icon_mail {
    width: 16px;
    display: inline-block;
    position: relative;
    float: right;
    margin-left: 10px;
}

.author_mail .icon_mail .tooltip_mail {
    width: 109px;
    height: 24px;
    background: url(images/graphics/mail_to_tg.png) no-repeat 0 0;
    position: absolute;
    top: -27px;
    left: -92px;
    display: none;
}

.block_tag {
    width: 100%;
    float: left;
    padding: 0 15px;
    margin-bottom: 15px;

    h4 {
        display: inline-block;
        font: 400 12px arial;

        a {
            padding: 5px;
        }
    }

    i {
        display: inline-block;
        vertical-align: middle;
    }

    a {
        display: inline-block;
        background: var(--bg-agray1);
        margin: 0 3px 5px 0;
        padding: 5.5px 10px;
        font-size: 14px;
        color: var(--color1);
    }

    span {
        @extend a;
        padding: 7px 10px;
        line-height: 0;
    }
}

.wrap_tag {
    width: calc(100% - 45px);
    float: right;
    white-space: nowrap;
    overflow-x: scroll;
}

/*luu_tin*/
.luu_baiviet a,
.luu_tin a {
    background: none;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    border: 1px solid var(--border);

    .ic-bookmark {
        @extend %ic;
        background-position: -234px -158px;
        width: 24px;
        height: 24px;
        margin: 3px;
        font-size: 0;
    }
}

.luu_baiviet,
.luu_tin {
    margin-left: 0;

    &.active {
        .ic-bookmark {
            background-position: -260px -158px;
        }
    }
}

/*End luu_tin*/

/**TAGS**/
.wrapper_tags {
    border-top: 1px solid #dddddd;
    padding-top: 15px;
}

/*Box Tags*/
.box_tags {
    .title_cate_video {
        margin-bottom: 20px;

        .txt_title_cate {
            font: bold 20px/1.3 var(--font1);

            a {
                color: var(--vne-color);
            }

            &:before {
                content: none;
            }
        }
    }

    .list_video_folder {
        border-bottom: 1px solid #eee;
        margin-bottom: 10px;

        &:last-of-type {
            border-bottom: none;
            margin-bottom: 0;
        }

        .item_video {
            &:last-child {
                margin-bottom: 15px;
                padding-bottom: 15px;
            }
        }
    }

    .tag_text {
        display: block;
        font: bold 18px/1.3 var(--font1);
        margin-bottom: 5px;
    }

    .tag_time {
        display: block;
        font-size: 16px;
        line-height: 1.31;
        font-weight: bold;
        margin-bottom: 10px;
    }
}

/*End Box Tags*/


.cate_date {
    font: 400 14px/1.29 arial;
    color: var(--gray3);
    margin: 10px 0;
    clear: both;
}

.cate_date a {
    color: var(--color1);
}

.wrap_item_top_detail .info_video_top {
    padding: 0 15px 5px 15px;
    margin-bottom: 15px;
}

.wrap_item_top_detail .inner_info_top {
    border-bottom: none;
}

.item_social,
.social_share .item_social {
    float: left;
    margin: 0 10px 0 0;
    position: relative;
}

.block_share_icon {
    margin-bottom: 5px;
}

.wrap_social {
    .item_social {
        color: var(--white);
        width: 32px;
        height: 32px;
        border-radius: 50%;
        line-height: 28px;
        text-align: center;
        border: 1px solid var(--border);

        i {
            color: var(--white);
        }
    }
}

.social_fb {
    background: #3d58a3;
}

.social_tw {
    background: #00aced;
}

.social_google {
    background: #dd4b38;
}

.luu_tin a {
    display: inline-block;
    vertical-align: top;
    color: #2c5e99;
}

.item_social_total {
    background: var(--border)ccc;
}

.item_social_total .count_share {
    position: absolute;
    top: 0;
    left: 35px;
    padding: 4px 5px;
    font: 700 13px arial;
    color: #9d224e;
    border: 1px solid #bbbbbb;
    border-radius: 3px;
}

.item_social_total .count_share .arrow_count {
    position: absolute;
    top: 50%;
    left: -8px;
    margin-top: -5px;
    width: 8px;
    height: 10px;
    background: url(images/graphics/arrow_count.jpg) no-repeat 0 0;
}

.title_cate_video {
    margin-bottom: 15px;
    border-bottom: 1px solid var(--border);
    padding-bottom: 5px;
}

.title_cate_video .txt_title_cate {
    font: 700 16px arial;
    color: #333;
    float: left;
}

.title_cate_video .txt_title_cate a {
    color: #333;
}

.play_button_player {
    position: absolute;
    top: 50%;
    margin-top: -32px;
    left: 50%;
    margin-left: -32px;
}

.play_button_player .ic-play {
    background-position: 0 -254px;
    width: 64px;
    height: 64px;
}

#pin_video_detail.sticky {
    z-index: 99;
}

/*COMMENT **/


/*COMMENT DARK**/
.darkmode {

    .ykien_vne,
    .input_comment {
        background: var(--bg-agray);
        color: #C9CCCE;

        a {
            color: #C9CCCE;
        }
    }

    .filter_coment {
        background: var(--bg-agray);
        border-top: 1px solid var(--border);

        a {
            color: #C9CCCE;
        }

        a.active {
            color: #C9CCCE !important;

            &::before {
                background: #C9CCCE;
            }
        }
    }

    .box_news_on_comment .info_news_on_com .title_on_com .title_news_com,
    .comment_item a.link_thich,
    .txt_blue,
    .box_comment_vne .view_more_coment a span,
    .block_not_login p a,
    .block_not_login p {
        color: #C9CCCE !important;
    }

    .input_comment textarea,
    .box_input_txt .input_anonymous,
    .box_news_on_comment {
        background: var(--bg-agray);
        border: 1px solid var(--border);
    }

    .comment_item.hight_light {
        background: var(--bg-agray)
    }

    .box_comment_vne .comment_item .txt_view_more {
        border-top: 1px solid var(--border);
    }

    .box_comment_vne .sub_comment .sub_comment_item {
        border-top: 1px dotted var(--border);
    }

    .box_comment_vne .view_more_coment a span {
        background: var(--bg-agray)
    }

    .box_comment_vne .view_more_coment:before {
        background: var(--border)
    }
}