* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box
}

body {
    max-width: 590px;
    margin: 0 auto;
    color: var(--colormain);
    font-family: arial, sans-serif;
    font-size: 14px;
    line-height: 1.5;
    background: var(--white);
}

.container {
    padding: 0 15px;
    width: 100%;
    float: left;
}

.width_common {
    width: 100%;
    float: left;
}

.inner_section {
    padding-left: 15px;
    padding-right: 15px;
}

.container_header {
    text-align: center;
    position: relative;
}

body.show_menu_full,
body.hidden {
    overflow: hidden;
    height: 100%;
}

a {
    color: var(--colormain);
    text-decoration: none;
    outline: none;
}

a:hover {
    text-decoration: none;
    color: #004f8b;
}

.icon-svg {
    width: 18px;
    height: 18px;
    fill: currentColor;
    vertical-align: middle;
    color: var(--gray)
}

img {
    border: 0px;
    font-size: 0;
    line-height: 0;
    max-width: 100%;
    vertical-align: middle;
}

ul,
li {
    list-style-type: none;
}

input,
textarea {
    font-family: arial;
    font-size: 11px;
    border: none;
    background: none;
}

.clear {
    clear: both;
    font-size: 0;
    line-height: 0;
}

.left {
    float: left;
}

.right {
    float: right;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
    margin: 0;
}

h1 {
    line-height: 24px;
}

h2,
h3,
h4 {
    line-height: 16px;
}

html {
    -webkit-text-size-adjust: none;
}

.txt_italic {
    font-style: italic;
}

.txt_center {
    text-align: center;
}

.txt_bold {
    font-weight: 700;
}

.txt_underline {
    text-decoration: underline;
}

.txt_fff {
    color: var(--white);
}

.txt_666 {
    color: #666;
}

.txt_333 {
    color: #333;
}

.txt_11 {
    font-size: 16px;
}

.relative {
    position: relative;
}

.space_bottom_5 {
    margin-bottom: 10px;
}

.mt5 {
    margin-top: 5px;
}

.mt10 {
    margin-top: 10px;
}

.mt15 {
    margin-top: 15px;
}

.mt20 {
    margin-top: 20px;
}

.mb10 {
    margin-bottom: 10px;
}

.mb15 {
    margin-bottom: 15px;
}

.mb20 {
    margin-bottom: 20px;
}

.space_bottom_10 {
    margin-bottom: 10px;
}

.space_bottom_20 {
    margin-bottom: 20px;
}

.space_bottom_30,
.mb30 {
    margin-bottom: 30px;
}

.space_left_0 {
    padding-left: 0;
}

.space_right_0 {
    padding-right: 0;
}


.thumb_img {
    display: block;
    overflow: hidden;
    height: 1px;
    position: relative;
    width: 100%;
    background: #f4f4f4;

    img {
        width: 100%;
        float: left;
        object-fit: cover;
    }
}

.thumb_5x3 {
    padding-bottom: 60%;
}

.thumb_16x9 {
    padding-bottom: 56.25%;

    .play_video {
        display: block;
    }
}

.thumb_1x1 {
    padding-bottom: 100%;
}

#to_top {
    width: 36px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    background: rgba(0, 0, 0, 0.1);
    position: fixed;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    z-index: 99;
    display: none;

    i {
        color: #666;
    }
}

.sticky {
    position: sticky;
    top: 0;
}

.clearfix {
    clear: both;

    &:before,
    &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}

.ic-other {
    background: url(images/icons/icon_other.png) no-repeat;
}

.ic-other.ic-hamberger {
    width: 17px;
    height: 12px;
    display: inline-block;
    vertical-align: middle;
    line-height: 45px;
    background-position: -22px 0;
}

.ic-other.ic-arrow-mn {
    width: 9px;
    height: 6px;
    display: inline-block;
    vertical-align: middle;
    background-position: -42px 0;
}

.ic-other.ic-close-menu {
    width: 16px;
    height: 17px;
    background-position: 0 0;
    display: inline-block;
    vertical-align: middle;
}

.ic-other.ic-back {
    width: 17px;
    height: 14px;
    background-position: 0 0;
    display: inline-block;
    vertical-align: middle;
    background-position: -76px 0;
}

.ic-other.ic-xemthem {
    width: 18px;
    height: 8px;
    background-position: 0 0;
    display: inline-block;
    vertical-align: middle;
    background-position: -94px 0;
}

.ic-comment {
    color: #9f224e;
    cursor: pointer;
}

.ic-x {
    font-size: 12px;
}

.ic-invert span {
    padding-left: 3px;
    font: 400 12px arial;
}

.ic-live {
    font-size: 9px;
    text-transform: uppercase;
    height: 15px;
    line-height: 15px;
    display: inline-block;
    vertical-align: middle;
    color: #e78dac;
}

.ic-live:before {
    -webkit-border-radius: 50%;
    border-radius: 50%;
    display: block;
    position: relative;
    width: 15px;
    height: 15px;
    content: '';
    margin-right: 5px;
    vertical-align: initial;
    -webkit-animation: live-pulse 1s infinite;
    -moz-animation: live-pulse 1s infinite;
    -o-animation: live-pulse 1s infinite;
    animation: live-pulse 1s infinite;
    float: left;
}

@-webkit-keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}

@-moz-keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}

@keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}