.navButton {
    position: absolute;
    top: 12px;
    left: 15px;
    z-index: 11;

    svg {
        width: 20px;
        height: 20px;
    }
}

.hamber_icon_video {
    svg {
        color: var(--gray2);
        margin-top: -3px;
    }
}

#box_menu_before {
    float: left;
    width: 100%;
}

header {
    height: 45px;
    position: relative;
    z-index: 999;
    border-bottom: 1px solid var(--border);
}

.logo_header {
    display: inline-block;
    margin-top: 8px;
}

.logo_header img {
    width: 150px;
}

.menu_bar {
    height: 41px;
    position: relative;
    z-index: 10;
}

.menu_bar .container {
    padding: 0 50px 0 15px;
}

.submenu_bar {
    height: 40px;
    position: sticky;
    z-index: 9;
    overflow: hidden;
    background: var(--white);
    transition-duration: 250ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    box-shadow: 0 1px 3px rgba(80, 80, 80, .2),
        0 3px 5px rgba(80, 80, 80, .15);

    &.sticky {
        z-index: 98;
        width: 100%;
        left: 0;

        &.nav-up {
            transform: translateY(-100%);
        }
    }
}



.navigation_scroll {
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.navigation_scroll ul {
    display: table-cell;
    vertical-align: middle;
}

.navigation_scroll ul li {
    display: table-cell;
    position: relative;
}

.navigation_scroll ul li+li:before {
    content: '';
    width: 1px;
    height: 17px;
    background: #ccc;
    position: absolute;
    right: 0;
    top: 10px;
}

.navigation_scroll ul li.first:before {
    content: '';
    width: 8px;
    height: 8px;
    border-top: 2px solid #d4d4d4;
    border-right: 2px solid #d4d4d4;
    position: absolute;
    right: -5px;
    top: 15px;
    transform: rotate(45deg)
}


.navigation_scroll ul li.first a {
    font-weight: bold;
    color: var(--gray1);
}

.navigation_scroll ul li:nth-child(2) a {
    color: var(--gray1);
}

.navigation_scroll ul li:last-child {
    padding-right: 40px;
    border-right: none;

    &:before {
        content: none;
    }
}

.navigation_scroll ul li a {
    height: 100%;
    padding: 0 15px;
    display: block;
    font: 400 16px/40px arial;
    color: var(--gray3);
}

.navigation_scroll ul li.active a {
    font-weight: 700;
    color: var(--vne-color);
}

.navigation_scroll ul li.sub_cat {
    position: relative;

    a {
        font-weight: bold !important;
    }
}

.navigation_scroll ul li.sub_cat:before {
    content: '';
    width: 10px;
    height: 1px;
    background: #d4d4d4;
    position: absolute;
    right: -5px;
    top: 17px;
    transform: rotate(45deg)
}

.navigation_scroll ul li.sub_cat:after {
    content: '';
    width: 10px;
    height: 1px;
    background: #d4d4d4;
    position: absolute;
    right: -5px;
    top: 24px;
    transform: rotate(-45deg)
}

.hamber_icon_video {
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    background: var(--bg-agray);
    z-index: 99;
}