.meta_post_art {
    color: var(--gray3);
    margin-top: 5px;

    .duration_time {
        float: left;
        font-size: 14px;

        i {
            margin-top: -1px;
        }
    }

    span {
        margin-left: 10px;
        font-size: 14px;
        margin-top: 0;

        a {
            color: var(--gray3);
        }
    }

    &>span {
        &:first-of-type {
            margin-left: 0;
            padding-left: 0;
        }
    }
}

.count_comment {
    .txt_num_comment {
        margin-left: 0;
    }

    .total {
        color: var(--blue);
        font-family: arial;
    }
}

.opacity_thumb {
    padding: 10% 0;
    width: 100%;
    position: absolute;
    bottom: -1px;
    left: 0;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
    opacity: 0.8;
}

.duration_video {
    position: absolute;
    left: 10px;
    bottom: 10px;
    font: 400 14px arial;
    color: #fff;

    .play_ico .ic-play {
        background: url(images/icons/play.svg);
        background-size: cover;
        width: 36px;
        height: 36px;
        margin-right: 4px;
    }

    .dash {
        opacity: 0.5;
        font-size: 12px;
        display: inline-block;
        vertical-align: middle;
    }

    .time_duration {
        display: inline-block;
        vertical-align: middle;
    }
}

.count_item_playlist {
    background: rgba(0, 0, 0, 0.8);
    width: 90px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    display: inline-block;

    .number_video {
        vertical-align: middle;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        width: 100%;
        text-align: center;
        font-size: 20px;
        color: #fff;
    }

    svg {
        color: #fff;
        width: 24px;
        height: 24px;
        display: block;
        margin: 0 auto;
    }
}

.art_section {
    margin-bottom: 20px;
    padding-bottom: 20px;
    clear: both;

    img {
        width: 100%;
    }

    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 15px;
        right: 15px;
        height: 1px;
        background: var(--border);
    }

    &.last-item {
        padding-bottom: 0;

        &::before {
            display: none;
        }
    }
}


.wrap_item_top,
.art_section {
    position: relative;

    .thumb_video {
        margin: 0;
        width: 100%;
    }

    h1,
    .title_news {
        font: bold 20px/1.6 var(--font1);
        padding: 10px 15px;
        clear: both;
        color: var(--vne-color);
    }

    h1 {
        a {
            color: var(--vne-color);
        }
    }

    h2,
    .short_intro {
        font-size: 16px;
        line-height: 1.5;
        padding: 0 15px;
        font-weight: normal;
        margin-bottom: 10px;
        clear: both;
    }

    .meta_post_art {
        padding: 0 15px;
    }
}

.wrap_item_top {
    .info-topic {
        padding: 0 15px;
    }
}

.art_section.inner_section {
    .title_news {
        padding: 0;
        margin-bottom: 10px;
        font-size: 18px;
    }

    .thumb_video {
        margin-bottom: 10px;
    }

    .short_intro,
    .meta_post_art {
        padding: 0
    }
}

.box_embed_video {
    padding-bottom: 56.25%;
    height: 0;
}

.info-topic {
    margin-bottom: 5px;

    a {
        color: var(--gray3);
        font-size: 12px;
        text-transform: uppercase;
    }
}

.item_video {
    width: 100%;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 30px;

    &:last-child,
    &.last_item_video {
        border: none;
    }

    .title_video a {
        font: 400 18px/1.3 arial;
        color: #333;

        &:visited {
            color: #333;
        }
    }
}

