.slide_box_topic {
    position: relative;
    margin-bottom: 25px;
    padding-bottom: 25px;

    .swiper-container {
        height: 100%;
    }

    &::before {
        content: '' !important;
        position: absolute;
        display: block !important;
        bottom: 0;
        left: 15px;
        right: 15px;
        height: 1px;
        background: var(--border);
    }

    .view-all {
        text-align: center;
        width: 100%;
        display: flex;
        height: 100%;
        background: #F7F7F7;
        align-items: center;
        justify-content: center;

        span {
            color: #076DB6;
            text-decoration-line: underline;
        }
    }

    .swiper-container {
        margin-right: -15px;
        padding-right: 45px;
    }

    .swiper-slide {
        height: auto;
    }

    .info-topic {
        margin-bottom: 10px;

        a {
            font: bold 18px/1.6 var(--font1);
            color: var(--colormain);
            text-transform: inherit;
        }
    }

    .art_section {
        padding-bottom: 0;
        margin-bottom: 0;

        &::before {
            display: none
        }

        .thumb_video {
            margin-bottom: 5px;
        }

        .short_intro {
            padding: 0
        }

        .duration_video {
            font-size: 12px;
            left: 5px;
            bottom: 5px;

            .play_ico {
                .ic-play {
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }

    &.box-placeholder {

        .info-topic.text {
            min-width: 245px;
            height: 29px;
        }

        .navigation_scroll {
            margin-right: -15px;

            .art_section {
                width: 160px;
                display: inline-block;
                margin-right: 15px;
            }
        }

        .title_video {
            margin-bottom: 5px;

            .text.line {
                margin-bottom: 5px;
            }
        }
    }
}

.no-border {
    padding-bottom: 0 !important;

    &::before {
        display: none !important;
    }
}

//End  slide_box_topic

.ScrollList_slide {
    z-index: 0;
    overflow: -moz-scrollbars-none;
    scrollbar-width: none;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-right: -15px;
    margin-left: -15px;

    &::-webkit-scrollbar {
        display: none;
        -webkit-appearance: none;
    }

    ul {
        position: relative;
        width: 100%;
        height: 100%;
        font-size: 0;
        padding: 0;
        white-space: nowrap;

        li {
            display: inline-block;
            position: static;
            height: 100%;
            font-size: medium;
            margin: 0 7.5px;
            width: calc(50% - 25px);
            white-space: normal;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            vertical-align: top;

            &>a {
                display: inline-block
            }

            &:first-child {
                margin-left: 15px;
            }

            &:last-child {
                margin-right: 15px;
            }

            .thumb_video {
                float: none;
            }

            .short_intro {
                display: box;
                display: -moz-box;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                -o-text-overflow: ellipsis;
                text-overflow: ellipsis;
                height: 42px;
                line-height: 1.4;
            }
        }
    }

    .view-all {
        position: absolute;
        top: 0;
        text-align: center;
        width: calc(50% - 25px);
    }
}

.box-placeholder {
    font-size: 0;
    line-height: 0;

    .text {
        display: inline-block;
        background-color: var(--color1);
        height: 15px;
        min-width: 100px;
        opacity: .1;
        animation: fading 1.5s infinite;

        &:first-child {
            margin-top: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &.link {
            background-color: var(--blue);
            opacity: .4;
        }

        &.line {
            width: 100%;
        }

        &.category {
            width: 100px;
            margin-bottom: 10px;
        }
    }

    h4.text {
        height: 20px;
        margin: 3px 0;
        opacity: .2;
    }

    .thumb_16x9 {
        background: var(--bg-agray1);
    }

    .title_video {
        padding: 0;
        line-height: 1.1;

        .text.line {
            margin-bottom: 10px;
            margin-top: 0;
        }
    }

    .text {
        height: 15px;
        margin-top: 0;
    }

    .title_video+.text {
        width: 50%;
    }

}

@keyframes fading {
    0% {
        opacity: .1;
    }

    50% {
        opacity: .2;
    }

    100% {
        opacity: .1;
    }
}


