.play-list {
    margin: 0px -15px 10px -15px;

    .button-list {
        background: #F7F7F7;
        padding: 0 15px;
        display: block;
        height: 40px;
        line-height: 40px;
        color: #222222;
        position: relative;
        border-bottom: 1px solid #eaeaea;

        &::before {
            content: "";
            position: absolute;
            right: 18px;
            width: 8px;
            top: 13px;
            height: 8px;
            border-top: 1px solid #757575;
            border-left: 1px solid #757575;
            transform: rotate(-135deg);
            transition: 0.4s ease;
        }

        svg {
            color: #4F4F4F;
            width: 16px;
            height: 16px;
            margin-right: 10px;
            vertical-align: text-bottom;
        }

        &.active {
            &::before {
                top: 15px;
                transform: rotate(45deg);
            }
        }
    }

    .list-video {
        display: none;
        margin: 15px 0;
        border-bottom: 1px solid #eaeaea;
    }
}

.list-video {
    float: left;
    width: 100%;

    .tag_time {
        display: block;
        font-size: 16px;
        line-height: 1.31;
        font-weight: bold;
        margin-bottom: 10px;
        padding: 0 15px;
    }

    .scrollbar-inner {
        max-height: 80vh;
        max-height: calc(100% - 67px)
    }

    .art_section {
        float: left;
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;
        margin-bottom: 15px;
        padding-bottom: 0;

        &::before {
            display: none
        }

        .thumb_video {
            width: 130px;
            float: left;
            margin-right: 10px;
            margin-bottom: 0 !important;
        }

        .content {
            overflow: hidden;
        }

        .short_intro {
            padding: 0;
            margin-bottom: 0;
            font-family: arial;
        }

        .duration_video {
            font-size: 12px;
            left: 5px;
            bottom: 5px;

            .play_ico {
                .ic-play {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        &.active {
            .thumb_video {
                position: relative;

                .thumb_img {
                    background: #222222;

                    img {
                        opacity: 0.1;
                    }
                }

                .duration_video {
                    display: none
                }

                &::before {
                    content: "ang pht";
                    color: #fff;
                    position: absolute;
                    z-index: 2;
                    top: 50%;
                    margin-top: -10px;
                    left: 0;
                    right: 0;
                    text-align: center;
                }
            }

            .title_news {
                a {
                    color: var(--vne-color);
                }
            }
        }
    }

    &__popup {
        position: fixed;
        top: 0;
        bottom: 0;
        background: #fff;
        z-index: 997;
        will-change: transform;
        -webkit-transition: .4s;
        transition: .4s;
        transform: translate(-100%);

        .art_section {
            margin-bottom: 15px;
            padding: 0 15px;

            &::before {
                display: none
            }
        }

        .duration_video {
            background: rgba(0, 0, 0, 0.75);
            color: #FFFFFF;
            padding: 4px 8px;
            left: 5px;
            bottom: 5px;
        }

    }
